$(document).ready(function() {

    $(".burger").click(function () {
        $(".header").toggleClass("_open")
        $("body").toggleClass("fixed-body")
    })

    $(".service-detail__more").click(function () {
        $(this).siblings(".service-detail__grid").toggleClass("_open")
    })

    $(".service-nav__show").click(function () {
        if($(this).parents(".service-nav__group").hasClass("_active")){
            $(".service-nav__group").removeClass("_active")
            $(".service-nav__hidden").slideUp()
        } else{
            $(".service-nav__group").removeClass("_active")
            $(".service-nav__hidden").slideUp()
            $(this).parents(".service-nav__group").addClass("_active")
            $(this).siblings(".service-nav__hidden").slideDown()
        }
    })

});
